@charset "UTF-8";
.hide-page-collect {
  display: none; }

.page-collect {
  position: fixed;
  top: 95px;
  left: 0;
  bottom: 80px;
  width: 100%;
  z-index: 0;
  overflow-y: scroll;
  color: #ebebeb;
  /* 定义滚动条高宽及背景
      高宽分别对应横竖滚动条的尺寸 */
  /* 定义滑块
      内阴影+圆角 */ }
  .page-collect ::-webkit-scrollbar {
    width: 3px; }
  .page-collect ::-webkit-scrollbar-thumb {
    background-color: rgba(214, 214, 214, 0.397); }
  .page-collect .left-nav {
    top: 95px;
    left: 30px;
    bottom: 80px;
    box-sizing: border-box;
    padding-top: 5px;
    padding-left: 5px;
    padding-right: 5px;
    width: 100%;
    background: rgba(255, 255, 255, 0.027);
    border-radius: 5px;
    overflow-y: scroll; }
    .page-collect .left-nav .title {
      margin-top: 10px;
      margin-bottom: 10px;
      padding-left: 5px;
      color: #9e9e9e;
      font-size: 13px; }

.left-nav ul {
  list-style-type: none;
  font-size: 14px;
  line-height: 20px; }
  .left-nav ul li {
    position: relative;
    box-sizing: border-box;
    padding: 0 20px 8px 30px;
    width: 223px;
    text-overflow: ellipsis;
    white-space: nowrap;
    overflow: hidden;
    cursor: pointer; }
    .left-nav ul li span {
      transition: all 0.2s; }
    .left-nav ul li:hover span {
      color: #f92e58; }
    .left-nav ul li:hover .icon-del {
      font-size: 15px; }
    .left-nav ul li .icon-yinleliebiao, .left-nav ul li .icon-will-love {
      position: absolute;
      left: 7px;
      font-size: 20px; }
    .left-nav ul li .icon-will-love {
      font-size: 15px; }
    .left-nav ul li .icon-del {
      position: absolute;
      right: 0;
      font-size: 0; }

.collect-container {
  width: 100%; }
  .collect-container .list-info {
    position: relative;
    display: flex;
    margin-bottom: 20px; }
    .collect-container .list-info .list-img {
      position: relative;
      flex-basis: 190px;
      height: 150px;
      overflow: hidden; }
      .collect-container .list-info .list-img .found-list .filter {
        position: absolute;
        width: 150px;
        height: 150px;
        background: rgba(0, 0, 0, 0.493); }
      .collect-container .list-info .list-img i {
        position: absolute;
        font-size: 80px;
        left: 36px;
        top: 30px;
        color: rgba(255, 255, 255, 0.705); }
      .collect-container .list-info .list-img img {
        width: 150px;
        min-height: 150px; }
    .collect-container .list-info .list-info-right {
      flex: 1; }
    .collect-container .list-info .name {
      font-size: 24px;
      box-sizing: border-box;
      margin-bottom: 10px; }
    .collect-container .list-info .desc {
      font-size: 13px;
      line-height: 24px;
      max-height: 72px;
      overflow-y: scroll;
      margin-bottom: 40px; }
    .collect-container .list-info .count {
      left: 10px;
      bottom: 0;
      font-size: 13px;
      color: #9e9e9e; }
      .collect-container .list-info .count p {
        display: inline-block;
        margin-right: 20px; }
        .collect-container .list-info .count p span {
          font-size: 16px; }
    .collect-container .list-info .play-btn {
      position: absolute;
      left: 100;
      bottom: 0;
      padding: 8px 10px;
      box-sizing: border-box;
      border: none;
      background: #ffffff11;
      color: #ebebeb;
      font-size: 13px;
      border-radius: 4px;
      outline: none;
      cursor: pointer;
      transition: all 0.3s; }
      .collect-container .list-info .play-btn:hover {
        color: #f92e58; }
      .collect-container .list-info .play-btn i {
        position: absolute;
        left: 8px;
        top: 8px;
        font-size: 14px; }
      .collect-container .list-info .play-btn p {
        padding-left: 15px; }
