@charset "UTF-8";
.hide-music-list-container {
  display: none; }

.music-list-container {
  position: fixed;
  top: 95px;
  bottom: 80px;
  left: 0;
  display: flex;
  width: 100%;
  box-sizing: border-box;
  padding-left: 25px;
  color: #ebebeb;
  overflow-y: scroll;
  z-index: 1; }
  .music-list-container .list-info {
    display: none;
    position: fixed;
    top: 95px;
    width: 200px;
    height: 200px;
    background: #fff; }
    .music-list-container .list-info .list-img {
      width: 200px;
      height: 200px;
      overflow: hidden; }
      .music-list-container .list-info .list-img img {
        width: 200px;
        min-height: 200px; }
    .music-list-container .list-info .control {
      padding-top: 5px; }
      .music-list-container .list-info .control i {
        position: relative;
        display: inline-block;
        font-size: 20px;
        color: #9e9e9e;
        cursor: pointer; }
        .music-list-container .list-info .control i:hover {
          color: #ebebeb; }
        .music-list-container .list-info .control i:hover:after {
          position: absolute;
          top: 25px;
          font-size: 12px; }
      .music-list-container .list-info .control .icon-play1 {
        padding-right: 8px; }
        .music-list-container .list-info .control .icon-play1:hover:after {
          content: "播放";
          left: 0; }
      .music-list-container .list-info .control .icon-addbox {
        padding-right: 10px; }
        .music-list-container .list-info .control .icon-addbox:hover:after {
          content: "添加到播放列表";
          left: -30px;
          width: 100px; }
      .music-list-container .list-info .control .icon-folder:hover:after {
        content: "添加到我的歌单";
        left: -30px;
        width: 100px; }
      .music-list-container .list-info .control .collected {
        color: #f92e58; }
    .music-list-container .list-info .name {
      margin-top: 10px;
      margin-bottom: 12px;
      line-height: 24px;
      font-size: 18px;
      font-weight: bold; }
    .music-list-container .list-info .album-info {
      margin-bottom: 12px;
      color: #9e9e9e; }
      .music-list-container .list-info .album-info .artist {
        margin-bottom: 5px;
        font-size: 16px; }
      .music-list-container .list-info .album-info .company {
        font-size: 13px; }
      .music-list-container .list-info .album-info .publish-time {
        margin-bottom: 5px;
        font-size: 13px; }
    .music-list-container .list-info .description {
      position: relative;
      font-size: 12px;
      line-height: 22px;
      width: 100%; }
  .music-list-container .show-list-container {
    position: absolute;
    left: 5%;
    right: 5%;
    width: 90%; }
